.country-card-shimmer {
  background-color: var(--shimmer-background);
  border-radius: 5px;
  width: 250px;
  height: 350px;
  padding: 1rem;
}

.country-shimmer-img {
  background-color: var(--shimmer-background-p);
  width: 100%;
  height: 50%;
}

.shimmer-header {
  background-color: var(--shimmer-background-p);
  border-radius: 6px;
  width: 100%;
  height: 25px;
}

.shimmer-p {
  background-color: var(--shimmer-background-p);
  border-radius: 6px;
  width: 90%;
  height: 15px;
}

.country-details-container {
  margin-inline: auto;
  max-width: 1200px;
  margin-top: 32px;
}

.back-button {
  cursor: pointer;
  border-radius: 4px;
  padding: 6px 24px;
  box-shadow: 0 0 4px #0003;
}

.dark .back-button {
  box-shadow: 0 0 4px #cecdcd33;
}

.country-details {
  align-items: center;
  gap: 64px;
  margin-top: 64px;
  display: flex;
}

.country-details img {
  border: 1px solid #ddd;
  width: 40%;
}

.details-text {
  flex-flow: column wrap;
  gap: 12px 64px;
  max-height: 180px;
  display: flex;
}

.details-text p {
  margin: 0;
}

.border-countries {
  flex-wrap: wrap;
  align-items: flex-start;
  gap: 8px;
  margin-top: 48px;
  display: flex;
}

.border-countries a {
  border-radius: 4px;
  padding: 4px 16px;
  font-size: 14px;
  box-shadow: 0 0 4px #0003;
}

@media (width <= 940px) {
  .country-details {
    flex-direction: column;
    align-items: flex-start;
    gap: 16px;
    margin-top: 32px;
  }

  .details-text {
    max-height: unset;
  }

  .country-details img {
    width: 100%;
    max-width: 400px;
  }
}

:root {
  --gray-color: #e4e0e0;
}

.country-details {
  max-width: 1200px;
  margin-inline: auto;
}

.shimmer-country-details {
  background-color: var(--gray-color);
  width: 400px;
  height: 500px;
}

.shimmer-country-name {
  background-color: var(--gray-color);
  width: 200px;
  height: 50px;
  margin: 3rem 0;
}

.shimmer-country-p {
  background-color: var(--gray-color);
